.lds-heart {
  display: flex;
  position: relative;
  width: 100px;
  height: 100px;
  margin: 0 auto;
  transform: rotate(45deg);
  transform-origin: 40px 40px;
}
.lds-heart div {
  align-self: center;
  top: 300%;
  left: 300%;
  position: absolute;
  width: 60px;
  height: 60px;
  background: red;
  animation: lds-heart 1.2s infinite cubic-bezier(0.215, 0.61, 0.355, 1);
}
.lds-heart div:after,
.lds-heart div:before {
  content: ' ';
  position: absolute;
  display: block;
  width: 60px;
  height: 60px;
  background: red;
}
.lds-heart div:before {
  left: -24px;
  border-radius: 50% 0 0 50%;
}
.lds-heart div:after {
  top: -24px;
  border-radius: 50% 50% 0 0;
}
@keyframes lds-heart {
  0% {
    transform: scale(0.95);
  }
  5% {
    transform: scale(1.1);
  }
  39% {
    transform: scale(0.85);
  }
  45% {
    transform: scale(1);
  }
  60% {
    transform: scale(0.95);
  }
  100% {
    transform: scale(0.9);
  }
}
